#About {
    padding: 0  20px;
}

.intro-section {
    justify-content: center;

    p {
        text-wrap: wrap;
    }

}


.my-pic {
    clip-path: circle();
    overflow: hidden;
    background-color: transparent;
    width: 100%;

    img {
        width: 100%;
    }

    &::before {
        content: '';
        aspect-ratio: 1/1;
        background-color: var(--sec-col);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 60%;
        clip-path: circle();
    }
}

.chron-slides {
    margin-left: 30px;

     * {
        background-color: transparent;
    }

    &::before {
        content: '';
        background-color: grey;
        position: absolute;
        height: 100%;
        top: 0;
        left: -15px;
        width: 1px;
    }

    .res-item {
        padding: 0 10px;
    }

    h4 {
        margin: 0;

        &::before {
            content: '';
            aspect-ratio: 1/1;
            background-color: var(--sec-col);
            clip-path: circle();
            position: absolute;
            width: 15px;
            top: 50%;
            transform: translateY(-50%);
            transition: transform .3s ease;
            left: -32.5px;
        }

        .res-item.active &::before {
            background-color: var(--prim-text);
            transform: scale(1.2) translateY(-50%);
        }
    }

    ul {
        color: #ccc
    }
}

small {
    color: grey;
}


.test-item, .service-item {
    margin: 10px;
    padding: 20px;
    background-color: var(--sec-col);
    border-radius: 1em;
    border: 1px solid var(--sec-col2)
}


.test-item .quote {

    &::after, &::before {
        content: '';
        border: 5px solid transparent;
        position: absolute;
    }

    &::before {
        border-left: 5px solid white;
        border-right: none;
        top: 10px;
        left: -10px;
    }

    &::after {
        border-left: none;
        border-right: 5px solid white;
        bottom: 5px;
        right: -10px;
    }
}

@media screen and (min-width: 768px) {
    .test-item, .service-item {
        width: 30%;
    }
}

@media screen and (max-width: 767px) and (min-width: 360px) {
    #resume-section, .intro-section {
        flex-direction: column;
    }

    .test-item, .service-item {
        width: 45%;
    }
}

@media screen and (max-width: 360px) {
    #resume-section, .intro-section {
        flex-direction: column;
    }

    .test-item, .service-item {
        width: 80%;
    }
}