.navbar {
    background-color: var(--main-bg);
    /* backdrop-filter: blur(5px); */
    left: 0px;
    position: sticky;
    top: 0px;
    transition: all .3s ease;
    z-index: 10;

    &.scroll {
        border-bottom: 1px solid var(--sec-col);
    }

    > *  {
        display: none;
        justify-content: space-between;
        padding: 10px;
    }
}


@media  screen and (min-width: 768px) {
    .lap-nav {
        display: flex;
    }
}

@media  screen and (max-width: 767px) {
    .mobile-nav {
        display: flex;
    }
}

.nav-item {
    color: grey;
    font-size: 110%;
    font-weight: 500;
    text-decoration: none;

    &.active, &:hover {
        color: var(--prim-text);
    }
}

.lap-nav .nav-item {
    margin: 0 10px;
    padding: 5px 10px;
    
    &::after {
        content: '';
        background-color: var(--prim-text);
        bottom: 0;
        height: 1px;
        left: 10px;
        position: absolute;
        transition: width .35s ease .15s;
        width: 0px;
    }

    &.active::after, &:hover::after {
        width: 50%;
    }
}

.mobile-nav .menu {
    backdrop-filter: blur(3px);
    background-color: #00000055;
    height: calc(100vh - 100%);
    left: 0;
    overflow: hidden;
    top: 100%;
    width: 100%;

    .wrapper {
        background-color: transparent;
        border-radius: 0 0 1em 1em;
        overflow: hidden;
        transition: height .3s ease;

        .close & {
            height: 0px;
        }
    }

    .content {
        background-color: var(--main-bg);
        border-bottom: 1px solid var(--sec-col);
        border-radius: 0 0 1em 1em;
        overflow: hidden auto;
        padding: 10px 0;
        padding-bottom: 30px;
    }
}

.mobile-nav .nav-item {
    padding: 15px 10px;

    &::before {
        content: '';
        background-color: var(--prim-text);
        border-radius: 2px;
        top: 33.3%;
        height: 33.3%;
        left: 2px;
        position: absolute;
        transition: transform .35s ease .1s;
        transform: scale(0.1);
        width: 2px;
    }
}

.mobile-nav .nav-item.active::before,
.mobile-nav .nav-item:hover::before {
    transform: scale(1);
}

.hero-title {
    font-size: 150%;
    font-weight: 650;
    letter-spacing: .5px;
    background: linear-gradient(to right, #888, #ccc, #aaa, #ddd);
    background-clip: text;
    color: transparent;
}