
.socials-list {

    &::after, &::before {
        content: "";
        /* background-color: red; */
        background-color: var(--sec-col);
    }
}

.me-img {
    .myPic-bg {
        aspect-ratio: 1/1;
        background-color: var(--sec-col);
        width: 60%;
        clip-path: circle();
    }

    .img-cont {
        background-color: transparent;
        clip-path: circle();
        overflow: hidden;
    }

    img {
        width: 100%;
    }
}

@media screen and (min-width: 768px) {
    .intro-txt {
        margin-left: 50px;
    }

    .socials-list {
        flex-direction: column;

        &::after {
            height: 100px;
            margin-top: 15px;
            width: 1px;
        }
    }
}

@media screen and (max-width: 768px) {

    .socials-list {
        bottom: 30px;

        &::before {
            height: 1px;
            margin-right: 15px;
            width: 50px;
        }
    }
}