:root {
	--main-bg: #eee;
	--sec-col: #ccc;
	--sec2-col: #aaa;
	--prim-text: black;
	--sec-text: white;

  background-color: var(--main-bg);
  color: var(--prim-text);
  font-size: 20px;

  &.dark-mode {
    --main-bg: #1b1b1b;
    --sec-col: #222;
    --sec-col2: #333;
    --tert-col: #5ad7ff;
    --prim-text: white;
    --sec-text: black;
  }
}


.hover:hover {
  &::before {
    content: attr(data-hover);
    color: var(--prim-text);
    position: absolute;
    /* max-width: 200%; */
    text-wrap: no-wrap;
    background-color: var(--sec-col);
    padding: 10px;
    border-radius: 1em;
    bottom: calc(100% + 10px);
    left: 0px;
    z-index: 10;
  }
}

