* {
    position: relative;
}

.bg-in {
    background-color: inherit;
}

.App, body {
    background-color: inherit;
    color: inherit;
    height: 100vh;
    overflow: hidden auto;
    width: 100vw;
    margin: 0px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.overlay {
    height: 100%;
    left: 0px;
    position: fixed;
    top: 0px;
    width: 100%;
}

.toast {
    align-items: center;
    border-radius: 1em;
    bottom: 5%;
    color: inherit;
    padding: .75em 1.5em;
    position: fixed;
    transition: opacity .5s ease-in-out;
    width: unset;
    z-index: 99;

    &[open] {
        display: flex;
    }

    &.close {
        opacity: 0;
    }
    .txt {
        font-size: 90%;
        font-weight: 500;
    }
    .img-txt {
        margin-left: 0px;
        font-size: 120%;
    }
}

.img-txt {
    background-size: cover;
    height: 1em;
    width: 1em;
    margin: 0 .5em;
}

body.still-loading {
    visibility: hidden;
    overflow: hidden;
}

.dropdown-menu {
    background-color: var(--head-col);
    padding: 10px 0;

    .dropdown-item {
        color: white;

        &:hover {
            background-color: var(--head2-col);
        }
    }
}

hr {
    border: 2px solid;
    border-color: var(--body3-col);
    background-color: var(--body3-col);
}

#loading-window.remove-window {
    opacity: 0;
    visibility: hidden;
    z-index: -10;
}

#loading-window {
    background-color: var(--main-col);
    left: 0px;
    position: fixed;
    top: 0px;
    height: 100vh;
    width: 100vw;
    transition: all 0.2s ease;
    opacity: 1;
    visibility: visible;
    z-index: 1000;

    .shortened-logo {
        width: 100px;
        aspect-ratio: 1/1;
        background: url("logo.svg"), var(--main-col);
        background-size: contain;
    }

    .full-logo {
        position: absolute;
        bottom: 5%;
        height: 25px;
        left: 50%;
        transform: translate(-50%, 0%);
    }
}

.fa-xmark{
    transition: transform .25s ease-in-out;

    &.plus {
        transform: rotate(-135deg);
    }
}


.crop-excess {
    text-overflow: ellipsis;
    overflow: hidden;
    text-wrap: nowrap;
}

.crop-excess-2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    display: -moz-box;
    -moz-box-orient: vertical;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}

.max, .fw {
    width: 100%;
}

.max, .fh {
    height: 100%;
}

.mega-max {
    height: 100vh;
    width: 100vw;
}

.crop-excess.hov:hover {
    overflow: visible;
    white-space: normal;
}

.hide {
    display: none !important;
}

.disappear {
    visibility: hidden !important;
}

.blur {
    filter: blur(5px);
}

.no-trans {
    transition: unset !important;
}

.no-btn {
    background-color: transparent;
    border: none;
    color: inherit;
    font-weight: 600;
    outline: none;
    padding: 5px;
}

.no-link {
    text-decoration: none;
}

.block {
    display: block;
}

.in-block {
    display: inline-block;
}

.flex,
.flex-col {
    display: flex;
}

.flex-col {
    flex-direction: column;
}

.mid-align {
    align-items: center;
}

.abs, .abs-mid {
    position: absolute;
}

.abs-mid {
    margin: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.center-text {
    text-align: center;
}

.left-text {
    text-align: left;
}

.dark-mode .sun {
    display: none;
}

.moon {
    display: none !important;

    .dark-mode & {
        display: block !important;
    }
}

.flex-col {
    flex-direction: column;
}
