.skill-item {
    margin: 5px;
    padding: 20px;
    background-color: var(--sec-col);
    box-shadow: 0 0 5px #222;
    border-radius: .5em;

    .skill-lvl {
        top: 0px;
        right: 0px;
        border-radius: 5px .5em 5px 5px;
        background-color: gold;
        font-weight: 700;
        font-size: 60%;
        padding: 2px 5px;
    }
}

@media  screen and (max-width: 360px) {
    .skill-item {
        width: calc(50% - 40px);
    }
}
